@media (min-width: 800px) {
  body {
    margin: 0;
    padding: 10px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
      "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
      "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  body::after {
    content: "";
    background: url("https://images.alphacoders.com/923/923543.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    opacity: 0.8;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: #fafafa;
  padding: 10px;
  height: 100%;
  margin-top: -8px;
  margin-left: -8px;
  margin-right: -8px;
}

.header {
  text-align: center;
  font: 1em/1.4 Roboto, "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 1.4rem;
  color: rgb(41, 73, 130);
}

.boxHeader {
  background-color: rgb(41, 73, 130);
  border-radius: 5px 5px 0px 0px;
  padding: 5px;
  color: #fff;
  font-size: 1.2rem;
  text-align: center;
  vertical-align: middle;
}

.box {
  border-radius: 5px;
  font: 1em/1.4 Roboto, "Helvetica Neue", Helvetica, Arial, sans-serif;
  background-color: #fff;
  margin: 20px auto;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2),
    0 1px 5px 0 rgba(0, 0, 0, 0.12);
  text-align: left;
  max-width: 500px;
  min-width: 220px;
}

.boxBody {
  padding: 10px;
  font-size: 1rem;
  color: #757575;
  text-align: center;
  font-weight: bold;
}

.button {
  background-color: rgb(41, 73, 130);
  color: #fff;
  border: none;
  border-radius: 5px;
  margin: 10px;
  height: 30px;
  width: 30px;
  font-size: 1.5rem;
  text-align: center;
}

.container {
  display: block;
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  font-size: 15px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  line-height: 190%;
  width: 100px;
  margin: 0 auto;
  padding-bottom: 10px;
}

.container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #fff;
  border: 1px solid rgb(41, 73, 130);
  border-radius: 5px;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.container .checkmark:after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 10px;
  border: solid rgb(41, 73, 130);
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

img {
  margin: 15px;
}

.pokemonInput {
  border: none;
  font: 1em/1.4 Roboto, "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: rgb(41, 73, 130);
  text-align: center;
  padding: 0;
}

.pokemonInput:focus {
  border: none;
  outline: none;
  padding: 0;
}

.pokemonLabel {
  border: 1px solid rgb(41, 73, 130);
  padding: 2px;
}

.pokemonLabel:focus {
  outline: none;
}

